/* style.css */

* {
  font-family: Consolas, Courier;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

.container {
  background-color: #f5f5f5;
  padding: 10px;
  width: 100%;
  -webkit-perspective: 600px;
          perspective: 600px;

  display: -ms-flexbox;

  display: flex;
}

.container .terminal {
  height: 27px; 
  -webkit-transition: 1s all cubic-bezier(0.65, 0.05, 0.36, 1); 
  -o-transition: 1s all cubic-bezier(0.65, 0.05, 0.36, 1); 
  transition: 1s all cubic-bezier(0.65, 0.05, 0.36, 1);

  width: 100%;
  background-color: #393b49;
  border-radius: 4px;
  position: relative;
  margin-right: 0;
  overflow: hidden;
}

.container .terminal:nth-child(2) {
  width: 0;
}

.container.second-window .terminal:first-child {
  margin-right: 10px;
}

.container.second-window .terminal:nth-child(2) {
  width: 100%;
}

.container.loaded .terminal {
  height: 100%;
}

.terminal .title-bar {
  height: 23px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background: -webkit-gradient(linear, left top, left bottom, from(#e8e8e8), to(#cacaca));
  background: -webkit-linear-gradient(top, #e8e8e8, #cacaca);
  background: -o-linear-gradient(top, #e8e8e8, #cacaca);
  background: linear-gradient(to bottom, #e8e8e8, #cacaca);
  padding-left: 5px;
  position: relative;
  z-index: 9999;
  display: -ms-flexbox;
  display: flex;
}

.terminal .title-bar .title-area {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  height: 23px;
  width: 100%;
  padding-right: 76px;
}

.terminal .title-bar .title-text {
  margin: 0 auto;
  padding-left: 20px;
}

.terminal .button-area {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  padding-left: 2px;
}

.terminal .button-area div {
  width: 14px;
  height: 14px;
  background-color: green;
  border-radius: 100%;
  margin-left: 6px;
  cursor: pointer;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.terminal .button-area div:nth-child(1) {
  margin-left: 0;
  background-color: #fc635d;
}

.terminal .button-area div:nth-child(2) {
  background-color: #fdbd41;
}

.terminal .button-area div:nth-child(3) {
  background-color: #35cb4d;
}

.terminal .button-area .icon {
  display: none;
  font-size: 9px;
  color: black;
  opacity: 0.75;
}

.terminal .button-area:hover .icon {
  display: block;
}

.terminal .content {
  font-family: Consolas, Courier;
  color: white;
  position: absolute;
  top: 23px;
  bottom: 0;
  overflow: scroll;
  width: 100%;
}

.terminal .content-wrapper {
  padding: 5px;
}

.terminal .content .input.hidden {
  display: none;
}

.terminal .content .input .directory {
  word-break: break-all;
}

.terminal .content .input .text {
  outline: none;
}

.terminal .directory span.user {
  color: #a9f6fe;
}


.terminal .directory span.computer {
  color: #71fa95;
}


.terminal .directory span.folder {
  color: #f8fea6;
}

.terminal .output .print {
  display: block;
}

.terminal .output .error {
  color: #ff6f6b;
}

.terminal .output .cmd-ls .item-dir,
.terminal .output .cmd-pwd .item-dir {
  color: #e4c4f9;
}

.terminal .output .cmd-ls .item-link {
  color: #1aa6b1;
}

.terminal pre {
  font-family: Courier, Consolas;
  margin: 0;
}


